 <code>#include &lt;iostream&gt;
using namespace std;
class Team {
public:
    string name;
    Team(const string &amp;n) {
        name = n;
    }
    void test(string name) {
        cout &lt;&lt; this-&gt;name &lt;&lt;'\t'&lt;&lt; name &lt;&lt; '\t' &lt;&lt; this &lt;&lt; endl;
    }
    void test1(string n) {
        cout &lt;&lt; this-&gt;name &lt;&lt;'\t'&lt;&lt; name &lt;&lt; '\t' &lt;&lt; this &lt;&lt; endl;
    }
};
int main()
{
    Team t1 = Team("Heat");
    Team t2 = Team("Bulls");
    cout &lt;&lt; &amp;t1 &lt;&lt; '\t' &lt;&lt; &amp;t2 &lt;&lt; endl;
    t1.test("t1");
    t2.test("t2");
    t1.test1("t1");
    t2.test1("t2");
    return 0;
}</code></pre>
