 <pre class="ne-codeblock language-cpp" data-language="cpp" id="IULin"><span style="font-weight:bold;">7.3</span>
 <code>#include &lt;iostream&gt;
using namespace std;
class Team {
public:
    int* score=NULL;
    Team(int n) {
        score = new int(n);
    }
    void test(string name) {
        cout &lt;&lt; *score &lt;&lt; endl;
    }
    ~Team() {
        if(score!=NULL)
            delete score;
    }
};
int main()
{
    Team *t1 = new Team(101);
    t1-&gt;test("t1");
    delete t1;
    return 0;
}</code></pre>
