 <pre class="ne-codeblock language-cpp" data-language="cpp" id="H5oaN"><span style="font-weight:bold;">7.6</span>virtual
 <code>#include&lt;iostream&gt;
using namespace std;
  
class base {
public:
    virtual void print() {
        cout &lt;&lt; "print base class\n";
    }  
    void show() {
        cout &lt;&lt; "show base class\n";
    }
};
  
class derived : public base {
public:
    void print() {
        cout &lt;&lt; "print derived class\n";
    }  
    void show()  {
        cout &lt;&lt; "show derived class\n";
    }
};
  
int main()
{
    base *bptr;
    derived d;
    bptr = &amp;d;  
    // Virtual function, binded at runtime
    bptr-&gt;print();  
    // Non-virtual function, binded at compile time
    bptr-&gt;show();    
    return 0;
}</code></pre>
