 <pre class="ne-codeblock language-cpp" data-language="cpp" id="DN8Ja"><span style="font-weight:bold;">7.7</span>
 <code>#include &lt;iostream&gt;
using namespace std;
// 
class Container {
public:
    virtual void pop()=0;    //
	// virtual void pop(){   //
    //    cout &lt;&lt; "Container Pop!\n" ;
    // }
};
// 
class Queue : public Container {
public:
    void pop() {
        cout &lt;&lt; "Queue: Pop the first element!\n" ;
    }
};
// 
class Stack : public Container {
public:
    void pop() {
        cout &lt;&lt; "Stack: Pop the last element!\n" ;
    }
};

void polymorphism(Container* container){
    container-&gt;pop();
}

int main() {
    Queue q;
    Stack s;
    q.pop();
    s.pop();
    Container* vq = &amp;q;
    vq-&gt;pop();
    Container* vs = &amp;s;
    vs-&gt;pop();
    polymorphism(vq);
    polymorphism(vs);
    return 0;
}</code></pre>
