 <pre class="ne-codeblock language-cpp" data-language="cpp" id="HhAyD"><span style="font-weight:bold;">7.9</span>
 <code>#include &lt;iostream&gt;
using namespace std;
// 
class Container {
public:
    static int count;
    Container() { ++count; }
    ~Container() { --count; }
};
// 
class Queue : public Container {
public:
    void pop() {
        cout &lt;&lt; "Pop the first element!\n" ;
    }
};
int Container::count = 0;
int main() {
    Container c;
    cout &lt;&lt; Container::count &lt;&lt; endl;
    cout &lt;&lt; c.count &lt;&lt; endl;
    Container arr[10];
    cout &lt;&lt; Container::count &lt;&lt; endl;
    Container *p = new Container();
    cout &lt;&lt; Container::count &lt;&lt; endl;
    Container *pp = new Container[5];
    cout &lt;&lt; Container::count &lt;&lt; endl;
    delete p;
    cout &lt;&lt; Container::count &lt;&lt; endl;
    delete[] pp;
    cout &lt;&lt; Container::count &lt;&lt; endl;
    Queue q;
    cout &lt;&lt; Container::count &lt;&lt; endl;
    return 0;
}</code></pre>
