 <pre class="ne-codeblock language-cpp" data-language="cpp" id="qOfzC"><span style="font-weight:bold;">7.10</span>
 <code>class Node
{
public:
    int val;								//int
    Node *next;								//
    Node(int val, Node *next = nullptr)
    {
        this-&gt;val = val;					//this
        this-&gt;next = next;
    }
};</code></pre>
 <p class="ne-p" id="u90735aea" style="text-indent: 2em">
  <span class="ne-text">
   privateIterator
  </span>
 </p>
 <pre class="ne-codeblock language-cpp" data-language="cpp" id="qVzkh"><span style="font-weight:bold;">7.11</span>
 <code>class Iterator{
private:
    Node* ptr;					//
public:
    Iterator(Node* node){
        ptr = node;
    }
    // 
    Iterator&amp; operator=(const Iterator&amp; it) {
        ptr = it.ptr;			//
        return *this;			//
    }
    // 
    bool operator==(const Iterator&amp; it) const {
        return ptr == it.ptr;	//
    }
    // 
    bool operator!=(const Iterator&amp; it) const {
        return ptr != it.ptr;	//
    }
    //
    Iterator&amp; operator++() {
        ptr = ptr-&gt;next;		//
        return *this;			//
    }
    //
    Iterator operator++(int) {
        Iterator tmp = *this;	//
        ++(*this);				//
        return tmp;
    }
    int operator*(){
        return ptr-&gt;val;		//
    }
};</code></pre>
 <p class="ne-p" id="u2a49c1b6" style="text-indent: 2em">
  <span class="ne-text">
   headbeginend
  </span>
 </p>
 <pre class="ne-codeblock language-cpp" data-language="cpp" id="uDSYC"><span style="font-weight:bold;">7.12</span>
 <code>class List{
private:
    Node* head;
public:
    List(){
        head = nullptr;
    }
    Iterator begin(){
        return Iterator(this-&gt;head);		//
    }
    Iterator end(){
        return Iterator(nullptr);			//
    }
    void push_back(int val){
        Node* node = new Node(val);			//
        if(!head)							//
            head = node;					//
        else{
            Node* ptr = head;
            while(ptr-&gt;next)				//
                ptr = ptr-&gt;next;			//
            ptr-&gt;next = node;				//
        }
    }
    bool insert(const Iterator&amp; it,int val){
        Node* tmp = head;
        while(Iterator(tmp)!=end() &amp;&amp; Iterator(tmp-&gt;next)!=it)	//
            tmp = tmp-&gt;next;				//
        if(Iterator(tmp)==end())  return false;	//
        Node* node = new Node(val);			//
        node-&gt;next = tmp-&gt;next;				//
        tmp-&gt;next = node;					//
        return true;
    }
    bool erase(const Iterator&amp; it){
        Node* tmp = head;
        while(Iterator(tmp)!=end() &amp;&amp; Iterator(tmp-&gt;next)!=it)	//
            tmp = tmp-&gt;next;				//
        if(Iterator(tmp)==end())  return false;	//
        Node* node = tmp-&gt;next;				//
        tmp-&gt;next = node-&gt;next;				//
        delete node;						//
        return true;
    }
};</code></pre>
