 <code>#include&lt;iostream&gt;
using namespace std;

int main(){
    int m;
    cin&gt;&gt;m;
    List ls;
    for(int i=0;i&lt;m;i++)
        ls.push_back(i+1);		//
    for(auto it = ls.begin();it!=ls.end();it++)
        cout&lt;&lt;*it&lt;&lt;' ';
    cout&lt;&lt;endl;
    auto it = ls.begin();		//
    it++;						//
    ls.insert(it,10);			//
    for(auto e:ls)				//
        cout&lt;&lt;e&lt;&lt;' ';
    cout&lt;&lt;endl;
    ls.erase(it);				//2
    for(auto e:ls)				//
        cout&lt;&lt;e&lt;&lt;' ';
    cout&lt;&lt;endl;
    return 0;
}</code></pre>
